.defaultBlock__calendar {
   width: 100%;
   display: -ms-flexbox;
   display: flex;
   -ms-flex-pack: center;
       justify-content: center;
   -ms-flex-wrap: wrap;
       flex-wrap: wrap;
}
.defaultBlock__calendar * {
   -webkit-box-sizing: border-box;
           box-sizing: border-box;
}
.defaultBlock__item-day {
   -ms-flex-preferred-size: calc(100%/7);
       flex-basis: calc(100%/7);
   height: 35px;
   display: -ms-flexbox;
   display: flex;
   background-color: #DFDFDF;
   border: 1px solid white;
}
.defaultBlock__item-day.current {
   border: 2px solid #ef6767;
   border-radius: 4px;
}
.defaultBlock__item-day:nth-child(7n),
.defaultBlock__item-day:nth-child(7n-1) {
   color: red;
}
.defaultBlock__item-day.prevNextDays {
   color: #aaa;
}
.defaultBlock__item-day:hover {
   cursor: pointer;
}
.defaultBlock__item-day.prevNextDays:hover {
   cursor: default;
}
.defaultBlock__item-day .day-num {
   width: auto;
   margin: auto;
}
.calendar .defaultBlock__title .monthName {
   padding: 0 10px;
}
.calendar .defaultBlock__title .prevMonth,
.calendar .defaultBlock__title .nextMonth {
   width: 25px;
   height: 25px;
   margin-top: 5px;
   background-size: 100%;
   background-repeat: no-repeat;
   background-image: url(../img/arrow.png);
   cursor: pointer;
}
.calendar .defaultBlock__title .prevMonth {
   -webkit-transform: rotate(180deg);
   -ms-transform: rotate(180deg);
   transform: rotate(180deg);
}



